import React, { useState, useEffect } from "react";
import Header from "./header/Header.js";
import AddPetForm from "./main/AddPetForm.js";
import Pet from "./main/Pet.js";
import Footer from "./footer/Footer";

import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

function App() {
  const [pets, setPets] = useState([]);

  // only run once the first time the component is rendered
  useEffect(() => {
    if (localStorage.getItem("petData")) {
      setPets(JSON.parse(localStorage.getItem("petData")));
    }
  }, []);

  // run every time our pet state changes
  useEffect(() => {
    localStorage.setItem("petData", JSON.stringify(pets));
  }, [pets]);

  return (
    <>
      <Header />
      <AddPetForm setPets={setPets} />
      <ul className="d-flex flex-md-row flex-wrap justify-content-around m-0 p-0 mb-5">
        {pets.map(pet => <Pet setPets={setPets} id={pet.id} name={pet.name} species={pet.species} age={pet.age} key={pet.id}/>)}
      </ul>
      <Footer />
    </>
  );
}

export default App;
