import React,  { useState } from 'react'
import Button from "react-bootstrap/Button";

const LikeArea = () => {
    const [likesCount, SetLikesCount] = useState(0);
    
    const increaseLikeHandler = () => {
      SetLikesCount((prev) => {
        return prev + 1;
      })
    }
    
    const decreaseLikeHandler = () => {
      SetLikesCount((prev) => {
        if (prev > 0){
          return prev - 1;
        }else{
          return 0;
        }
        
      })
    }
    
    return (
      <>
        <h5>This page has been liked <span className="fw-bolder text-danger">{likesCount}</span> times</h5>
        <Button variant="outline-dark" onClick={increaseLikeHandler} className="m-3">Like</Button>
        <Button variant="outline-dark" onClick={decreaseLikeHandler} className="m-3">Unlike</Button>
      </>
    )
  }

export default LikeArea
